html {
  overflow: hidden;
  height: 100%;    
}

body {
  font-family: 'Roboto';
  background-position: fixed;
  background-position: center;
  background-size: cover;
  overflow: auto;
  background: #67b26f;
  background: -webkit-linear-gradient(to top, #67b26f, #4ca2cd);
  background: -webkit-gradient(linear, left bottom, left top, from(#67b26f), to(#4ca2cd));
  background: -webkit-linear-gradient(bottom, #67b26f, #4ca2cd);
  background: -o-linear-gradient(bottom, #67b26f, #4ca2cd);
  background: linear-gradient(to top, #67b26f, #4ca2cd); 
  height: 100%;
}

.ui.menu {
  font-family: 'Lato', sans-serif;
  letter-spacing: 3px;
  margin: 20px auto;
}

#testi {
  position: fixed !important;
  width: 40%;

}

h3 {
  font-family: 'Roboto';
  font-size: 20px;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 2px;
  opacity: 0.7;
  padding-left: 10px;
  padding-top: 5px;
}

.adding {
  padding-top: 5px;
  width: 80%;
  -ms-flex-item-align: center;
      align-self: center;
  margin: 0 auto;
}

option, select {
  text-transform: capitalize;
}

.container {
  font-family: 'Roboto';
  width: 40%;
  font-size: 1.1vw;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
  margin: 30px auto;
  -webkit-transition-duration: ease-in;
       -o-transition-duration: ease-in;
          transition-duration: ease-in;
}

.cont {
  margin: 10px auto;
  word-wrap: break-word;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: #FFFFFF;
  -webkit-box-shadow: 0px 1px 3px 0 rgba(34, 36, 38, 0.15);
          box-shadow: 0px 1px 3px 0 rgba(34, 36, 38, 0.15);
  margin: 1rem 0em;
  padding: 1em 1em;
  border-radius: 0.28571429rem;
  border: 1px solid rgba(34, 36, 38, 0.15);
  font-size: 16px;
  text-align: justify;
}



.navbar-text {
  font-family: 'Roboto';
  font-weight: 300;
  text-transform: uppercase;
  margin-right: 25px;
  text-shadow: 0px 0px 1px rgba(0,0,0,0.4);
  opacity: 0.4;
  font-size: 14px;
}

form {
  border: none;
  margin: 10px 10px;
  opacity: 0.9;
}

input, select, textarea {
  margin-bottom: 5px;
  outline: none;
}

textarea { 
  resize: vertical;
  max-height: 200px;
}

#submit {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 13px;
  background-color: #81cfc5;
}

#submit:hover {
  background-color: #cfe9e5;
  color: black;
}

#species {
  text-transform: capitalize;
}

@media (max-width: 767px) {
  .container {
      width: 80%;
      font-size: 20px;
  }

}

@media (max-width: 1024px) and (min-width: 768px) {
  .container {
      width: 60%;
      font-size: 16px;
  }
}


